@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

header {
  background-color: #25b79f;
  height: 100vh;
}

.container {
  max-width: 1200px;
  width: 90%;
  margin: auto;
}

/* Navbar */
.navbar {
  position: fixed;
  width: 100%;
  z-index: 500;
}

.navbar-container {
  justify-content: space-between;
  align-items: center;
  height: 64px;
}

.menu-items {
  display: flex;
}

.menu-items li {
  list-style: none;
  padding: 1rem;
}

.menu-items a {
  text-decoration: none;
  color: #f0f0e6;
  font-size: 1.2rem;
  padding: 0.3rem;
}

.menu-items a:hover {
  border-bottom: 2px solid #f0f0e6;
}

.navbar .logo {
  order: 1;
  color: #f0f0e6;
  font-size: 10rem;
}

.toggle-btn {
  background: none;
  border: none;
  color: #f0f0e6;
  cursor: pointer;
}

/* Home Content */
.home-content .name {
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #07374a;
  width: 100%;
  text-align: center;
}

.home-content .name h1 {
  font-size: 2.2rem;
  text-align: center;
}

.home-content .name p {
  color: #f0f0e6;
  font-size: 1.5rem;
}

/* About Me */
.about-me {
  background-color: #07374a;
  padding: 6rem 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}


.light-mode {
  background-image: url('https://i.pinimg.com/originals/a5/6e/2b/a56e2be84b74028b409628c9b300e5b2.gif');
  color: black;
}

.dark-mode {
  background-image: url('https://media0.giphy.com/media/v1.Y2lkPTc5MGI3NjExNDF4cGxnd2g3OXhjdHJxbjRjdHkweHphN2pmcDNoeDN4Y29xeTRraCZlcD12MV9naWZzX3NlYXJjaCZjdD1n/qgQUggAC3Pfv687qPC/giphy.webp');
  color: white;
}

.light-text {
  color: black;
}

.dark-text {
  color: white;
}



.about-me .about-heading {
  margin-bottom: 20px;
  color: #25b79f;
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
}

.about-content img {
  margin-bottom: 30px;
  height: 200px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
}

.about-content p {
  font-size: 1.2rem;
  color: #f0f0e6;
  padding: 0 2.3rem;
  text-align: center;
  margin-bottom: 30px;
}

/* Skills */
.skills {
  background-color: #25b79f;
  padding: 6rem 0;
}

.skills-heading {
  font-size: 2rem;
  font-weight: 600;
  color: #25b79f;
  text-align: center;
}

.skills-list {
  list-style: none;
  padding: 0;
  text-align: center;
}

.skills-list li {
  font-size: 1.2rem;
  color: #f0f0e6;
  padding: 0.5rem 0;
}

/* Projects */
.portfolio {
  background: #25b79f;
  padding: 6rem 0;
}

.proj-heading h1 {
  text-align: center;
  color: #1d93a0;
  font-size: 2.3rem;
  font-weight: 700;
  margin: 3rem 0;
}

.portfolio-content {
  overflow-x: auto;
  white-space: nowrap;
  padding: 20px;
}

.flashcards {
  display: flex;
  flex-direction:row;
  gap: 40px;
  align-items: center;
}

.flashcard {
  background: #1d93a0;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 8px;
  min-width: 350px;
  max-width: 350px;
  height: 575px;
  text-align: left;
  display: inline-block;
  overflow: hidden;
  white-space: normal; 
  transition: transform 0.3s ease;
}

.flashcard:hover {
  transform: scale(1.05); 
}

.flashcard img {
  width: 100%;
  height:auto;
  border-radius: 10px;
  opacity: 0.75;
}

.flashcard h2 {
  font-size: 1.3em;
  margin: 10px 0;
  white-space: normal; 
  text-align: center;
  margin-top: 20px;
}

.flashcard p {
  font-size: 0.9em;
  line-height: 1.4;
  overflow:visible;
  max-height: 180px;
  white-space: normal;
  margin-top: 20px;
  text-align: center; 
}

/* Contact */
.contact {
  background: #07374a;
  position: relative;
  height: 92vh;
}

.contact-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.contact-content h2 {
  font-size: 2.5rem;
  font-weight: 400;
  color: #25b79f;
  padding-bottom: 0.5rem;
}

.contact-content .mail {
  color: #f0f0e6;
  padding-bottom: 0.2rem;
  font-size: 1rem;
}

.contact-content .links {
  color: #25b79f;
  padding: 0.5rem;
  padding-bottom: 1.8rem;
  font-size: 1rem;
}

.contact-content a {
  text-decoration: none;
  color: #25b79f;
  padding: 0.5rem;
  transition: 0.3s ease-in-out;
}

.contact-content a:hover {
  color: #cece79;
}

.dark-mode header {
  background-color: #07374a;
}

.dark-mode .home-content .name {
  color: #f0f0e6;
}

.dark-mode .about-me {
  background-color: #25b79f;
}

.dark-mode .about-me .about-heading {
  color: #07374a;
}

.dark-mode .skills {
  background-color: #07374a;
}

.dark-mode .portfolio {
  background-color: #07374a;
}

.dark-mode .proj-heading h1 {
  color: #25b79f;
}

.dark-mode .contact {
  background-color: #25b79f;
}

.dark-mode .contact-content h2 {
  color: #07374a;
}

/* Media Queries */
@media (max-width: 900px) {
  .navbar-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .menu-items {
    flex-direction: column;
    width: 100%;
  }

  .menu-items li {
    width: 100%;
    text-align: center;
    margin: 5px 0;
  }

  .portfolio .container {
    flex-direction: column;
  }

  .flashcards {
    display: flex;
    flex-direction:column;
    gap: 50px;
    align-items: center;
  }

  .flashcard {
    background: #1d93a0;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 8px;
    min-width: 400px;
    max-width: 500px;
    height: 610px;
    text-align: left;
    display: inline-block;
    overflow: hidden;
    white-space: normal; 
    transition: transform 0.3s ease;
  }
  
  .flashcard:hover {
    transform: scale(1.05); 
  }
  
  .flashcard img {
    width: 100%;
    height:auto;
    border-radius: 10px;
    opacity: 0.75;
  }
  
  .flashcard h2 {
    font-size: 1.3em;
    margin: 10px 0;
    white-space: normal; 
    text-align: center;
    margin-top: 20px;
  }
  
  .flashcard p {
    font-size: 0.9em;
    line-height: 1.4;
    overflow:visible;
    max-height: 180px;
    white-space: normal;
    margin-top: 20px;
    text-align: center; 
  }

  .contact .container {
    flex-direction: column;
  }

  .skills-list {
    flex-direction: column;
  }

  .skills-list li {
    width: 100%;
    text-align: center;
  }

  .home-content .name h1 {
    font-size: 1.8rem;
  }

  .home-content .name p {
    font-size: 1.2rem;
  }

  .about-me .about-heading {
    font-size: 1.5rem;
  }

  .about-content p {
    font-size: 1rem;
    padding: 0 1rem;
  }

  .skills-heading {
    font-size: 1.5rem;
  }

  .skills-list li {
    font-size: 1rem;
  }

  .proj-heading h1 {
    font-size: 1.8rem;
  }

  .contact-content h2 {
    font-size: 2rem;
  }

  .contact-content .mail,
  .contact-content .links,
  .contact-content a {
    font-size: 1.2rem;
  }
}

@media (max-width: 400px) {
  .navbar-container {
    height: auto;
  }

  .navbar .logo {
    font-size: 1.5rem;
  }

  .menu-items a {
    font-size: 0.8rem;
  }

  .home-content .name h1 {
    font-size: 1.2rem;
  }

  .home-content .name p {
    font-size: 1rem;
  }

  .about-me .about-heading {
    font-size: 1rem;
  }

  .about-content p {
    font-size: 0.8rem;
    padding: 0 0.5rem;
  }

  .skills-heading {
    font-size: 1rem;
  }

  .skills-list li {
    font-size: 0.8rem;
  }

  .proj-heading h1 {
    font-size: 1.2rem;
  }

  .flashcards {
    display: flex;
    flex-direction:column;
    gap: 40px;
    align-items: center;
  }

  .flashcard {
    background: #1d93a0;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 8px;
    min-width: 350px;
    max-width: 350px;
    height: 600px;
    text-align: left;
    display: inline-block;
    overflow: hidden;
    white-space: normal; 
    transition: transform 0.3s ease;
  }
  
  .flashcard:hover {
    transform: scale(1.05); 
  }
  
  .flashcard img {
    width: 100%;
    height:auto;
    border-radius: 10px;
    opacity: 0.75;
  }
  
  .flashcard h2 {
    font-size: 1.3em;
    margin: 10px 0;
    white-space: normal; 
    text-align: center;
    margin-top: 20px;
  }
  
  .flashcard p {
    font-size: 0.9em;
    line-height: 1.4;
    overflow:visible;
    max-height: 180px;
    white-space: normal;
    margin-top: 20px;
    text-align: center; 
  }

  .contact-content h2 {
    font-size: 1.5rem;
  }

  .contact-content .mail,
  .contact-content .links,
  .contact-content a {
    font-size: 1 rem;
  }
}
